import { FunctionComponent, useMemo } from "react";
import CSS, { Property } from "csstype";

type LosAngelesLAType = {
  losAngelesLA?: string;

  /** Style props */
  losAngelesLAAccentColor?: Property.AccentColor;
};

const LosAngelesLA: FunctionComponent<LosAngelesLAType> = ({
  losAngelesLA,
  losAngelesLAAccentColor,
}) => {
  const losAngelesLAStyle: CSS.Properties = useMemo(() => {
    return {
      accentColor: losAngelesLAAccentColor,
    };
  }, [losAngelesLAAccentColor]);

  return (
    <div
      className="flex-1 relative text-[11.96px] tracking-[0.11px] leading-[17.94px] text-gray-300 text-left"
      style={losAngelesLAStyle}
    >
      {losAngelesLA}
    </div>
  );
};

export default LosAngelesLA;
